@tailwind base;
@tailwind components;
@tailwind utilities;

/* Применение кастомных цветов */
body {
    background-color: theme('colors.primary.light'); /* Использовать светлый цвет по умолчанию */
}

body.dark {
    background-color: theme('colors.primary.dark'); /* Использовать темный цвет для темной темы */
}
/* Основной стиль для сетки точек */
.dot-grid {
    width: 100px; /* Размер сетки, можно подстроить под нужный */
    height: 100px;
    display: grid;
    grid-template-columns: repeat(5, 9.59px);
    grid-template-rows: repeat(5, 10px);
    gap: 10px;
    position: absolute; /* Для того чтобы можно было располагать в любом месте */
}

.dot-grid div {
    width: 9.59px;
    height: 10px;
    border-radius: 50%;
    animation: pulse 1.5s infinite;
}

/* Анимация пульсации */
@keyframes pulse {
    0% {
        transform: scale(1);
        opacity: 1;
    }
    50% {
        transform: scale(1.3);
        opacity: 0.7;
    }
    100% {
        transform: scale(1);
        opacity: 1;
    }
}

/* Для синего набора точек */
.blue div {
    background: #0B50FF;
}

/* Для фиолетового набора точек */
.purple div {
    background: #EB00FF;
}

/* Для оранжевого набора точек */
.orange div {
    background: #FF5C00;
}

/* Пример расположения */
.blue {
    top: 50px;
    left: 50px;
}

.purple {
    top: 200px;
    left: 150px;
}

.orange {
    top: 350px;
    left: 250px;
}

